.cars-section {
  width: 100%;
  padding-block: 100px;
  background-color: #dc7c33;
  text-align: center;
  color: white;
  box-shadow: 1px -5px 20px 0px rgba(0, 0, 0, 0.2);
}
.cars-section .title {
  font-size: 25px;
  margin-block-end: 70px;
}

.container-item {
  max-width: 80%;
  margin: auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  gap: 20px;
}
.item {
  max-width: 300px;
  height: 400px;
  position: relative;
  cursor: pointer;
  transition: transform 1ms ease-in;
}
.item:hover {
  transform: scale(1.1);
}
.item,
.item div {
  display: flex;
  align-items: center;
  justify-content: center;
}
.item div {
  position: absolute;
  z-index: 2;
  color: white;
  background-color: #00000078;
  width: 100%;
  height: 100%;
  border-radius: 8px;
}
.item img {
  width: 100%;
  height: 100%;
  border-radius: 8px;
  filter: grayscale(100%);
  z-index: 1;
}

.item:hover img {
  filter: none;
}

.container-hidden {
  margin: auto;
  margin-top: 70px;
  max-width: 900px;
  text-align: start;
}
